import Vue from 'vue'
import  VueRouter from 'vue-router'
Vue.use(VueRouter)

const register=()=>import('../views/mine/Register')
const login=()=>import('../views/mine/Login')
const Rank=()=>import('../views/rank/Rank')
const person_file=()=>import('../views/mine/Personfile')
const clockin=()=>import('../components/component/clockin/Dayclockin')
const Root=()=>import('../components/common/tabbar/Root')
const record=()=>import('../components/component/clockin/Punch_in_record')
const article=()=>import('../components/component/acticle/Publicactive')
const person_article=()=>import('../components/component/acticle/personarticle')
const details_article=()=>import('../components/component/acticle/Detailsarticle')
// const Community_clock_in=()=>import('../components/component/community_clock_in/Community_clock_in')
// const Community_blog=()=>import('../components/component/community_clock_in/Community_blog')
const routes=[
    // {
    //     path:'',
    //     redirect:'/person_file',
    //     meta:{
    //         title:'注册'
    //     }
    // },
    {
        path:'/register',
        component:register,
        meta:{
                title:'注册'
            }
    },
    {
        path:'/login',
        component:login,
        meta:{
            title:'登录'
        }
    },
    {
        path:'/person_file',
        component:person_file,
        meta:{
            title:'个人中心'
        }
    },
    {
        path:'/clockin',
        component:clockin,
        meta:{
            title:'每日打卡'
        }
    },
    {
        path:'/record',
        component:record,
        meta:{
            title:'打卡记录'
        }
    },
    {
        path:'/article',
        component:article,
        meta:{
            title:'发布文章'
        }
    },
    {
        path:'/person_article',
        component:person_article,
        meta:{
            title:'我的文章'
        }
    },
    {
        path:'/details_article',
        component:details_article,
        meta:{
            title:'文章详情'
        }
    },
    {
        path:'/Rank',
        component:Rank,
        meta:{
            title:'排行榜'
        }
    },
    {
        path:'/',
        component:Root
    },
]
const router =new VueRouter({
    routes,
    // mode:'history'
});
